@font-face {
  font-display: swap;
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/alumnisans-600.woff2") format("woff2"), url("../fonts/alumnisans-600.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/alumnisans-700.woff2") format("woff2"), url("../fonts/alumnisans-700.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/alumnisans-800.woff2") format("woff2"), url("../fonts/alumnisans-800.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/robotocondensed-300.woff2") format("woff2"), url("../fonts/robotocondensed-300.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/robotocondensed-400.woff2") format("woff2"), url("../fonts/robotocondensed-400.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/robotocondensed-700.woff2") format("woff2"), url("../fonts/robotocondensed-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  min-width: 320px;
  overflow-wrap: anywhere;
  scroll-behavior: smooth;
  font-size: 16px;
  line-height: 1.5;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 1200px) and (max-width: 1280px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  padding: 20px 24px 19px;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  color: inherit;
  background-color: transparent;
  border: 2px solid;
  border-radius: 0;
  cursor: pointer;
  user-select: none;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  padding-top: 30px;
  background-color: #b80027;
  background: conic-gradient(from 130.31deg at calc(50% - 465px) 257px, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
}
@media (max-width: 1199px) {
  .header {
    padding-top: 40px;
    background: conic-gradient(from 139.4deg at 160px 198px, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 15px;
    background: conic-gradient(from 171.43deg at 113px 168px, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
}

.header__links {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
}

.header__logo {
  margin-top: 10px;
}
@media (max-width: 1199px) {
  .header__logo {
    width: 82px;
    margin-top: 7px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 69px;
    margin-top: 5px;
  }
}

.header__link {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .header__link {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .header__link {
    margin-bottom: 73px;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }
}

.header__primery-unit {
  margin-top: -63px;
}
@media (max-width: 1199px) {
  .header__primery-unit {
    margin-top: 0;
  }
}

@media (hover: hover) {
  .logo:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.logo.focus-visible:focus {
  opacity: 0.8;
}

.primery-unit {
  display: grid;
  grid-template-rows: 1fr min-content min-content;
  grid-template-columns: 133px 246px 1fr;
  grid-template-areas: "features city image" "features title image" "features btn image";
  gap: 13px 16px;
  min-height: 658px;
  padding-top: 51px;
  padding-bottom: 51px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .primery-unit {
    grid-template-columns: 113px 187px 1fr;
    gap: 12px 24px;
    min-height: auto;
    padding-top: 19px;
    padding-bottom: 82px;
  }
}
@media (max-width: 767px) {
  .primery-unit {
    grid-template-areas: ". city" "features title" "btn btn";
    grid-template-columns: 113px 1fr;
    column-gap: 12px;
    padding-top: 80px;
    padding-bottom: 80px;
  }
}

.primery-unit__city,
.primery-unit__title {
  font-weight: 600;
  font-family: "Alumni Sans", "Arial", sans-serif;
}

.primery-unit__city {
  grid-area: city;
  align-self: end;
  margin: 0 0 0 64px;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  text-align: center;
  text-transform: lowercase;
}
@media (max-width: 1199px) {
  .primery-unit__city {
    margin: 0 4px -5px 0;
    font-size: 34px;
    line-height: 41px;
    text-align: right;
  }
}
@media (max-width: 767px) {
  .primery-unit__city {
    margin: -6px 3px 2px 0;
    font-size: 28px;
    line-height: 34px;
  }
}

.primery-unit__title {
  grid-area: title;
  margin: 0 0 39px;
  font-size: 92px;
  line-height: 110px;
  letter-spacing: 0.05em;
  text-align: right;
  text-transform: uppercase;
  overflow-wrap: normal;
}
@media (max-width: 1199px) {
  .primery-unit__title {
    margin-bottom: 27px;
    font-size: 72px;
    line-height: 86px;
    text-transform: none;
  }
}
@media (max-width: 767px) {
  .primery-unit__title {
    margin-bottom: 14px;
    margin-left: -22px;
  }
}

.primery-unit__features {
  grid-area: features;
  align-self: center;
  margin: 0;
  padding: 117px 0 0;
  list-style: none;
  font-size: 14px;
  text-align: right;
}
@media (max-width: 1199px) {
  .primery-unit__features {
    padding-top: 44px;
  }
}
@media (max-width: 767px) {
  .primery-unit__features {
    align-self: start;
    padding: 78px 0 30px;
  }
}

.primery-unit__btn {
  grid-area: btn;
  margin-bottom: 45px;
  margin-left: 16px;
  outline: 2px solid;
}
@media (hover: hover) {
  .primery-unit__btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.primery-unit__btn.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 1199px) {
  .primery-unit__btn {
    margin-bottom: 25px;
    margin-left: 4px;
  }
}
@media (max-width: 767px) {
  .primery-unit__btn {
    margin-bottom: 0;
    margin-left: 0;
  }
}

.primery-unit__image {
  grid-area: image;
  margin-left: 49px;
}
@media (max-width: 1199px) {
  .primery-unit__image {
    margin-left: 6px;
  }
}
@media (max-width: 767px) {
  .primery-unit__image {
    display: none;
  }
}

.about {
  padding-top: 100px;
  padding-bottom: 100px;
}

